import React, { useState, useEffect } from 'react';
import { postList } from './Data';
import { Link } from 'react-router-dom';
import "../../css/Board/PostList.css";

const PostList = (props) => {
    const [ dataList, setDataList ] = useState();
    
    useEffect(()=>{
      if(localStorage.getItem('data')){
        setDataList(JSON.parse(localStorage.getItem('data')));
      }else{
        setDataList(postList);
      }
    }, [ ])
  
    return (
      <section className="post-section">
        <div>Í≤åÏãúÌåê</div>
        <table className="table">
            <thead>
            <tr className="table-head">
                <th>Í∏ÄÎ≤àÌò∏</th>
                <th>Ï†úÎ™©</th>
                <th>Îì±Î°ùÏùº</th>
                <th>Ï°∞ÌöåÏàò</th>
            </tr>
            </thead>
            <tbody className="table-body">
                {
                    dataList ? dataList.map((item) => {
                    return (
                        <tr key={item.no}>
                            <td>{ item.no }</td>
                            <td> <Link to={`/Board/postDetail/${item.no}`}>{ item.title }</Link></td>
                            <td>{ item.createDate }</td>
                            <td>{ item.readCount }</td>
                        </tr>
                    )
                    }) : 
                    <tr>
                      <td></td>
                    </tr>
                }
            </tbody>
        </table>
        <button className="post-add-btn"><Link to={'/Board/postAdd'}>Í≤åÏãúÍ∏Ä Ï∂îÍ∞Ä</Link></button>
      </section>
    )
  }
  
  export default PostList;